<template>
	<div class="section">
		<baidu-map class="map" :center="bMap.center" :zoom="bMap.zoom" @ready="handler">
			<bm-scale :anchor="bMap.positions[3]"></bm-scale>
			<bm-navigation :anchor="bMap.positions[3]"></bm-navigation>
			<bm-map-type :map-types="bMap.mapTypes" :anchor="bMap.positions[1]"></bm-map-type>
			<bm-city-list :anchor="bMap.positions[0]"></bm-city-list>
			<bm-overview-map :anchor="bMap.positions[2]" :isOpen="true"></bm-overview-map>
		</baidu-map>
		<section-one :dataOne="chargingService"></section-one>
	</div>
</template>

<script>
	import sectionOne from '../common/section-one.vue';
	export default {
		components: {
			sectionOne
		},
		data() {
			return {
				chargingService: [],
				bMap: {
					center: '',
					zoom: 3,
					positions: ['BMAP_ANCHOR_TOP_LEFT', 'BMAP_ANCHOR_TOP_RIGHT', 'BMAP_ANCHOR_BOTTOM_LEFT', 'BMAP_ANCHOR_BOTTOM_RIGHT'],
					mapTypes: ['BMAP_NORMAL_MAP', 'BMAP_HYBRID_MAP']
				}
			}
		},
		created () {
	  	this.$axios.get('/api/chargingService').then(res => {   //请求接口webName数据
				this.chargingService = res.data.data;
				console.log(this.chargingService);
			}).catch(err => {
				console.log(err);
			});
	  },
		methods: {
			handler({
				BMap,
				map
			}) {
				console.log(BMap, map)
				//this.center.lng = 116.404
				//this.center.lat = 39.915
				this.bMap.center = '西安'
				this.bMap.zoom = 15
			}
		},
		computed: {
		
		}
	}
</script>

<style>
	.section{
		width: 100%;
		height: 100%;
	}
	.map {
		width: 100%;
		height: 100%;
	}
</style>


